<template lang="pug">
    div.content
        div.list(v-show="!isShowAvatorInfo")
            //- 头像编辑
            div.list-block
                ul 
                    li.item-content(@click="showAvatorInfo")
                        div.item-inner
                            div.item-title 头像
                            div.item-after
                                img.avator(:src="user.head_image || ''")
            //- 编辑一
            div.list-block
                ul
                    li.item-content.item-link(@click="formEdit(user.nick_name, '16', 'nick_name', '昵称只能免费更改一次，超过次数将收取1000000秀豆')")
                        div.item-inner
                            div.item-title 昵称
                            div.item-after(v-text="user.nick_name || ''") 
                    li.item-content
                        div.item-inner
                            div.item-title 账号
                            div.item-after(v-text="user.user_id || ''")
                    li.item-content(@click="sexEdit()")
                        div.item-inner
                            div.item-title 性别
                            div.item-after
                                img.icon-sex(v-if="user.sex == 1" src="../../../assets/img/sex-man.png")
                                img.icon-sex(v-else src="../../../assets/img/sex-woman.png")
                    li.item-content.item-link(@click="formEdit(user.signature, '33', 'signature')")
                        div.item-inner
                            div.item-title 个性签名
                            div.item-after(v-text="user.signature || ''")
            //- 编辑二
            div.list-block
                ul
                    li.item-content.item-link(@click="pickerOpen()")
                        div.item-inner
                            div.item-title 生日
                            div.item-after(v-text="user.birthday || ''")
                    li.item-content.item-link(@click="emotionalEdit")
                        div.item-inner
                            div.item-title 情感状态
                            div.item-after(v-text="user.emotional_state || ''")
                    li.item-content.item-link(@click="showCityPicker")
                        div.item-inner
                            div.item-title 家乡
                            div.item-after(v-text="`${user.province || ''} ${user.city || ''}`")
                    li.item-content.item-link(@click="formEdit(user.job, '16', 'job')")
                        div.item-inner
                            div.item-title 职业
                            div.item-after(v-text="user.job || ''")
            div.button-group
                mt-button.mint-button-fill(
                    type="primary"
                    @click.native="userSave"
                ) 保存
        div.avator-info(
            v-show="isShowAvatorInfo"
        )
            div.avator-info-inner
                div.inner-top
                    span.close(@click="showAvatorInfo") <
                    img.avator-big(:src="user.head_image")
                div.inner-bottom
                    //- vue-core-image-upload.upload-img(
                    //-     class="btn btn-primary"
                    //-     crop="local"
                    //-     cropRatio="1:1"
                    //-     :cropBtn="cropBtn"
                    //-     @imageuploading="imageuploading"
                    //-     @imageuploaded="imageuploaded"
                    //-     :max-file-size="5242880"
                    //-     :url="doUploadUrl"
                    //-     text="选择图片"
                    //-     inputOfFile="head_image"
                    //-     extensions="png,jpg,gif"
                    //- )
                    vue-core-image-upload.upload-img(
                        class="btn btn-primary"
                        @imageuploading="imageuploading"
                        @imageuploaded="imageuploaded"
                        :max-file-size="5242880"
                        :url="doUploadUrl"
                        text="选择图片"
                        inputOfFile="head_image"
                        extensions="image/*"
                    )
                    span.span-close(@click="showAvatorInfo") X
        
        //- 文本编辑
        mt-popup(
            v-model="popupFormVisible"
            position="right"
        )    
            div.form-block
                input(
                    :placeholder="`请输入小于${limit}个文字`"
                    v-model="editText"
                    :maxlength="limit"
                )
                p.form-tip
                    span(v-text="`${editText.length}/${limit}`")
                p.form-tip.form-tip-1(v-if="nick_info")
                    span(v-text="nick_info")
                div.button-group
                    mt-button.mint-button-fill(
                        type="primary"
                        @click.native="save"
                    ) 保存
                    mt-button.mint-button-fill(
                        type="default"
                        @click.native="cancel"
                    ) 取消

        //- 性别编辑
        mt-popup(
            v-model="popupSexVisible"
            position="right"
        )    
            div.form-block.form-sex-block
                ul.sex-list
                    li.sex-item(:class="{ 'active' : user.sex == 1 }" @click="selectSex(1)")
                        img(src="../../../assets/img/set-sex-man.png")
                        label 男
                    li.sex-item(:class="{ 'active' : user.sex == 2 }" @click="selectSex(2)")
                        img(src="../../../assets/img/set-sex-woman.png")
                        label 女
                div.button-group
                    mt-button.mint-button-fill(
                        type="primary"
                        @click.native="saveSexForm"
                    ) 保存
                    mt-button.mint-button-fill(
                        type="default"
                        @click.native="cancelSexForm"
                    ) 取消
        mt-popup(
            v-model="popupEmotionalVisible"
            position="bottom"
        )
            div.form-emotional
                div.form-emotional-inner
                    span.emotional-i(:class="{'active' : activeEmotionalStatus == item}" v-for="item in emotionalStatus" v-text="item" @click="selectemotional(item)")
        mt-datetime-picker(
            v-model="pickerValue"
            type="date"
            value=""
            ref="picker"
            year-format="{value} 年"
            month-format="{value} 月"
            date-format="{value} 日"
            @confirm="handleConfirm"
            :startDate="startDate"
            :endDate="endDate"
        )
        mt-popup(
            v-model="popupAddressVisible"
            position="bottom"
        )
            div.city-picker
                mt-picker(
                    :slots="slots"
                    @change="onAddressChange"
                )
</template>
<script>
import VueCoreImageUpload from 'vue-core-image-upload'
import { Toast, Indicator } from 'mint-ui'
import queryString from 'queryString'
import address from '@/assets/js/city.json'
import { mapActions } from 'vuex'
export default {
    name: 'userEdit',
    components: {
        'vue-core-image-upload': VueCoreImageUpload
    },
    data() {
        return {
            cropBtn: { ok: '确定', 'cancel': '取消' },
            isShowAvatorInfo: false,
            user: '',
            nick_info: '',
            pickerVisible: true,
            popupFormVisible: false,
            popupSexVisible: false,
            popupEmotionalVisible: false,
            popupAddressVisible: false,
            limit: '',
            editText: '',
            editData: '',
            pickerValue: '',
            startDate: new Date(1960, 0, 1, 0, 0, 0),
            endDate: new Date(),
            emotionalStatus: ['保密', '单身', '恋爱中', '已婚', '同性'],
            activeEmotionalStatus: '',
        }
    },
    created() {
        this.getData();
    },
    methods: {
        ...mapActions(['setUserInfo']),
        getData() {
            let axiosUrl = process.env.NODE_ENV == 'development' ? '/gm/api/userEdit' : this.api.userEdit();
            this.axios.get(axiosUrl).then(res => {
                if (res.data.status == 1) {
                    this.user = res.data.user;
                    this.nick_info = res.data.nick_info;
                    this.activeEmotionalStatus = res.data.user.emotional_state;
                    this.oldSex = res.data.user.sex;
                }
                else {
                    Toast(res.data.error || '操作失败');
                }
            })
                .catch(err => {
                    console.log(err);
                });
        },
        imageuploading() {
            // 图片上传过程中
            Indicator.open('图片上传中...');
        },
        imageuploaded(result) {
            // 图片上传完
            Indicator.close();
            if (result.state == 200) {
                this.axios.post(this.api.doUpdate(), queryString.stringify({
                    oss_path: result.result
                })).then(res => {
                    if (res.data.status == 1) {
                        this.user.head_image = result.fullname;
                        Toast(res.data.error || '操作成功');
                    }
                    else {
                        Toast(res.data.error || '操作失败');
                    }
                })
                    .catch(err => {
                        Toast(err || '请求失败');
                    });
            }
            else {
                Toast(result.message || '操作失败');
            }
        },
        showAvatorInfo() {
            this.isShowAvatorInfo = !this.isShowAvatorInfo;
        },
        formEdit(data, limit, editData) {
            this.popupFormVisible = !this.popupFormVisible;
            this.limit = limit;
            this.editData = editData;
            this.editText = data;
        },
        sexEdit(sex) {
            this.popupSexVisible = !this.popupSexVisible;
        },
        selectSex(sex) {
            this.user.sex = sex;
        },
        save() {
            if (!$.trim(this.editText)) {
                Toast('请输入文本内容');
                return false;
            }
            if ($.trim(this.editText).length > this.limit) {
                Toast('输入的文字不能超过最大长度');
                return false;
            }
            let self = this;
            switch (this.editData) {
                case 'nick_name':
                    self.user.nick_name = self.editText;
                    break;
                case 'signature':
                    self.user.signature = self.editText;
                    break;
                case 'job':
                    self.user.job = self.editText;
                    break;

                default:
                    break;
            }
            self.popupFormVisible = false;
        },
        cancel() {
            this.popupFormVisible = false;
        },
        saveSexForm() {
            if (this.user.is_edit_sex == 0) {
                this.user.sex = this.oldSex;
                this.Toast('性别只能编辑一次');
            }
            this.popupSexVisible = false;
        },
        cancelSexForm() {
            this.popupSexVisible = false;
            this.user.sex = this.oldSex;
        },
        showDatetimepicker() {
            this.pickerVisible = true;
        },
        handleConfirm() {
            let date = new Date(this.pickerValue);
            let year = date.getFullYear();
            let month = date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1;
            let day = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
            this.user.birthday = year + "-" + month + "-" + day;
        },
        pickerOpen() {
            this.$refs.picker.open();
        },
        emotionalEdit() {
            // 编辑情感状态
            this.popupEmotionalVisible = !this.popupEmotionalVisible;
        },
        selectemotional(status) {
            // 选择情感状态
            this.activeEmotionalStatus = this.user.emotional_state = status;
            this.popupEmotionalVisible = false;
        },
        userSave() {
            let self = this;
            this.axios.post(this.api.userSave(), queryString.stringify(this.user)).then(res => {
                if (res.data.status == 1) {
                    this.axios.get(this.api.getUserHome()).then(res => {
                        this.setUserInfo(res.data.user);
                        let objToast = Toast(res.data.error || '操作成功');

                        setTimeout(function() {
                            objToast.close();
                            self.$router.replace({
                                path: '/user/index'
                            });
                        }, 1000);
                    });
                }
                else {
                    Toast(res.data.error || '操作失败');
                }
            })
                .catch(err => {
                    Toast(err || '网络异常，请求失败');
                });
        },
        onAddressChange(picker, values) {
            values[0] && (this.user.province = values[0]);
            values[1] && (this.user.city = values[1]);
            picker.setSlotValues(1, address[values[0]]);
        },
        showCityPicker() {
            this.popupAddressVisible = !this.popupAddressVisible;
        }
    },
    computed: {
        doUploadUrl() {
            return this.api.doUpload();
        },
        slots() {
            return [
                {
                    flex: 1,
                    values: Object.keys(address),
                    className: 'slot1',
                    textAlign: 'center'
                }, {
                    divider: true,
                    content: '-',
                    className: 'slot2'
                }, {
                    flex: 1,
                    values: ['北京'],
                    className: 'slot3',
                    textAlign: 'center'
                }
            ];
        }
    }
}
</script>

<style lang="less">
@import '../../../assets/css/variable.less';
.button-group {
    padding: 0 .5rem;
    margin-top: 1rem;
}

.avator {
    width: 1.75rem;
    height: 1.75rem;
    border-radius: 1.75rem;
}

.list-block {
    .item-title {
        flex-shrink: 0;
    }
    .item-after {
        max-height: initial;
        flex-shrink: 1;
        white-space: initial;
    }
}

.avator-info {
    .inner-top {
        position: relative;
        width: 100vw;
        height: 100vw;
        .avator-big {
            width: 100%;
            height: 100%;
        }
        .close {
            position: absolute;
            top: .5rem;
            left: .5rem;
            width: 2.5rem;
            height: 2.5rem;
            line-height: 2.5rem;
            text-align: center;
            display: block;
            border-radius: 2.5rem;
            background-color: rgba(0, 0, 0, .5);
            color: #fff;
            font-family: serif;
            font-size: 1.2rem;
        }
    }
}

.avator-info-inner {
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.upload-img {
    height: 2rem;
    line-height: 2rem;
    text-align: center;
    color: #fff;
    background-color: @color-theme;
    border-radius: 2rem;
}

.inner-bottom {
    padding: 0 1rem;
    text-align: center;
    .span-close {
        height: 2.5rem;
        line-height: 2.5rem;
        display: block;
        font-size: .8rem;
    }
}

.form-block {
    width: 100vw;
    height: 100vh;
    background-color: #fafafa;
    input {
        border: 0;
        height: 2rem;
        padding: 0 .5rem;
        width: 100%;
    }
    .form-tip {
        padding: 0 .5rem;
        text-align: right;
        margin-top: .5rem;
        color: @fc-light;
    }
    .form-tip-1 {
        font-size: .6rem;
    }
    .mint-button {
        margin-bottom: .5rem;
    }
}

// .v-modal {
//     display: none;
// }
.form-emotional {
    width: 100vw;
    background-color: #fff;
    .form-emotional-inner {
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        padding: .5rem;
    }
    .emotional-i {
        width: 3.75rem;
        height: 2rem;
        line-height: 2rem;
        text-align: center;
        display: block;
        background-color: #e5e5e5;
        margin: .25rem;
        border-radius: 5px;
        &.active {
            background-color: @color-yellow;
            color: #fff;
        }
    }
}

.city-picker {
    width: 100vw;
}

.form-sex-block {
    display: flex;
    flex-direction: column;
    .sex-list {
        position: relative;
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        .sex-item {
            display: flex;
            flex-direction: column;
            align-items: center;
            margin: .75rem 0;
            color: @fc-dark;
            img {
                width: 4rem;
                height: 4rem;
                border-radius: 4rem;
                -webkit-filter: grayscale(100%);
                -moz-filter: grayscale(100%);
                -ms-filter: grayscale(100%);
                -o-filter: grayscale(100%);

                filter: grayscale(100%);

                filter: gray;
            }
            &.active {
                img {
                    -webkit-filter: grayscale(0%);
                    -moz-filter: grayscale(0%);
                    -ms-filter: grayscale(0%);
                    -o-filter: grayscale(0%);

                    filter: grayscale(0%);

                    filter: gray;
                }
            }
        }
    }
    .button-group {
        flex-shrink: 0;
    }
}
</style>



